import React from 'react';
import logo from '../assets/ootw_logo.png'

export const Header = (props) => (
  <header>
    <div className='container flexbox'>
      <div className='header-logo'>
        <img className='header-logo-image' src={logo} alt="mars application logotype"/>
      </div>
      <div className='header-welcome'>
        <h1 className='header-welcome-title'>{ props.welcomeTitle }</h1>
        <p className='header-welcome-text'>{ props.welcomeText }</p>
      </div>
    </div>
  </header>
)

export default Header;
