import React, { Component } from 'react';
import axios from 'axios';

import Header from './components/HeaderComponent';
import Form from './components/form/FormComponent';
import Divider from './components/shared/DividerComponent';
import ResultList from './components/results/ResultListComponent';
import Footer from './components/FooterComponent';

import camerasData from '../src/data/cameras.json';
import { NASA_API_URL, API_KEY} from '../src/constants';

import './sass/app.scss';

class App extends Component {
  state = {
    ready: false, 
    rovers: [],
    cameras: camerasData.cameras,
    results: [],    
  }
  
  renderResults() {
    return (
      <>
        <Header 
          welcomeTitle={"Welcome to Mars App"}
          welcomeText={"This simple app allows you to browse all the photos taken by martian rovers. Thanks to NASA API integration you can see what was seen by Curiosity, Opportunity and Spirit rovers. Enjoy!"}
        />
        <section id="form">
          <Form 
            rovers={this.state.rovers} 
            cameras={this.state.cameras} 
            handleSubmit={this.handleSubmit}
          />
        </section>
        <Divider />
        { this.state.results.length > 0 &&
          <section id="results">
            <ResultList results={this.state.results}/>
          </section>
        }
        <Divider />
        <Footer />
      </>
    );
  }

  renderLoadingPage() {
    return (
      <>
        <Header 
          welcomeTitle={"Welcome to Mars App"}
          welcomeText={"Your app is loading..."}
        />
        <Footer />
      </>
    )
  }

  render() {
    if(this.state.ready) {
      return this.renderResults();
    } else {
      return this.renderLoadingPage();
    }
  }
 
  componentDidMount() {
    this.props.rovers.map((rover) => {
      axios.get(`https://api.nasa.gov/mars-photos/api/v1/manifests/${rover}?api_key=I2tK77U4tFT03b0JW1q8zgPzVe4AAGeew4lK7cWb`)
      .then((res) => {
        this.setState({
          rovers: [...this.state.rovers, res.data.photo_manifest],
          ready: Boolean(this.state.rovers.length)
        })
        if (this.state.rovers.length === this.props.rovers.length) {
          this.setState({
            ready: true
          })
        }
      })
    })
  }

  handleSubmit = (roverInput, dateInput, cameraInput) => {
    const camera = cameraInput === 'All' ? '' : `&camera=${cameraInput}` // if cameraInput === all do not add camera to url!
    const url = `${NASA_API_URL}/rovers/${roverInput}/photos?earth_date=${dateInput}${camera}&api_key=${API_KEY}`
    axios.get(url)
    .then(res => {
      this.setState({
        results: res.data.photos,
        renderResults: true,
      })
    })
  }
}
 
export default App;
